.user-menifestation-lst {
    display: flex;
    margin: 50px;
    border-radius: 5px;
    background-color:#afc08e;
    padding: 20px;

}

.post-btn {
    font-size: 10px;
    letter-spacing: 2px;
    font-weight: 200 !important;
    background-color: #535F2D;
    color: #fff;
    padding: 10px 15px;
    border-radius: 3%;
    margin: auto;
    text-align: center;
    display: inline-block !important;
    text-decoration: none;
    border: 0px;
    width: max-content;
    cursor: pointer;
    transition: all 0.3s 0s ease-in-out;
  

  }
  .post-btn:hover {
    background-color: #616e34; 
    color: white;
    }

